

function getItems(authState) {
    const data = {
        items: [
            {
                id: 'navigation',
                title: 'Dashboards',
                type: 'group',
                icon: 'icon-navigation',
                children: [
                    {
                        id: 'dashboard',
                        title: 'Dashboard',
                        type: 'item',
                        url: '/dashboard/investment',
                        icon: 'feather icon-trending-up',
                    },
                ]
            },
            {
                id: 'ui-element',
                title: 'Feeds',
                type: 'group',
                icon: 'icon-calendar',
                children: [
                    {
                        id: 'case-feed',
                        title: "Case Feed",
                        type: 'item',
                        icon: 'feather icon-alert-circle',
                        url: '/case-feed',
                    },
                ],
            },
        ]
    };

    if (authState.isAuthenticated && authState.user !== null) {
        const { user } = authState;
        data.items.push(
            {
                id: 'auth',
                title: 'Account',
                type: 'group',
                icon: 'key',
                children: [
                    {
                        id: 'profile',
                        title: `${user.first_name} ${user.last_name}`,
                        type: 'item',
                        icon: 'feather icon-user',
                        url: '/profile',
                    },
                    {
                        id: 'signout',
                        title: "Sign Out",
                        type: 'item',
                        icon: 'feather icon-slash',
                        url: '/auth/signout-1',
                    },
                ]
            }
        )
    } else if (!authState.loading) {
        data.items.push(
            {
                id: 'auth',
                title: 'Account',
                type: 'group',
                icon: 'key',
                children: [
                    {
                        id: 'signin',
                        title: "Sign In",
                        type: 'item',
                        icon: 'feather icon-user',
                        url: '/auth/signin-1',
                    },
                    {
                        id: 'signup',
                        title: "Sign Up",
                        type: 'item',
                        icon: 'feather icon-user-plus',
                        url: '/auth/signup-1',
                    },
                ]
            }
        )
    }
    return data;
}

export { getItems };